.find__container {
    width: 85vw;
    margin: 0 auto;

    
    .form{
        display: grid;
        place-items: center;
        margin: 1rem auto;
        margin-top: 2.5rem;
    
        .form__content{
            display: grid;
            row-gap: 1.5rem;
        }
        .form__input,
        .form__label,
        .form__submit{
            border: 0;
            outline: none;
            font-size: .938rem;
        }
        .form__box{
            width: 280px;
            height: 59px;
            position: relative;
        }
        .form__shadow{
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: #000;
        }
        .form__input{
            position: absolute;
            border: 2.5px solid #000;
            background-color: #fff;
            width: 100%;
            height: 100%;
            z-index: 10;
            padding: 18px;
            transition: transform .3s;
    
            &.form::placeholder{
                transition: opacity .5s;
            }
        }
        .form__label{
            z-index: 100;
            position: absolute;
            top: 16px;
            left: 20px;
            font-size: .563rem;
            font-size: hsl(79, 100%, 49%);
            font-weight: 600;
            transition: .2s;
            pointer-events: none;
            opacity: 0;
        }
        .form__button{
            justify-self: flex-end;
            background-color: #000;
        }
        .form__submit{
            padding: .875rem 1.5rem;
            color: #000;
            background-color: hsl(79, 100%, 49%);
            cursor: pointer;
            transition: transform .3s;
    
            &:hover{
                transform: translate(-6px, -6px);
            }
        }
        .form__input:focus::placeholder{
            opacity: 0;
            transition: .3s;
        }
        .form__input:focus,
        .form__input:not(:placeholder-shown).form__input:not(:focus){
            transform: translate(-8px, -8px);
            padding: 28px 18px 18px;
            animation: input-animation .5s;
        }
        .form__input:focus + .form__label,
        .form__input:not(:placeholder-shown).form__input:not(:focus) + .form__label{
            opacity: 1;
            top: 2px;
            left: 12px;
            transition: .3s;
        }
    }
    
    @keyframes input-animation{
        0%{
            transform: translate(0);
        }
        40%{
            transform: translate(-9px, -9px);
        }
        60%{
            transform: translate(-7px, -7px);
        }
    }

    
    
    .meal__list__container {
        display: flex;
        justify-content: center;
        width: 100%;
        .loading__container {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 2rem;
            width: 85vw;
            height: 50vh;
            margin: 0 auto;
            padding: 1rem;
            font-size: 2rem;
            img {
                width: 40px;
                margin-right: 1rem;
            }
        }
    }
}

@media screen and (max-width: 340px){
    .form{
        &.form__content,
        &.form__box{
            width: 100%;
        }
    }
}

/* For Large devices */
@media screen and (min-width: 968px){
    .form__content{
        zoom: 1.1;
    }
}